.about {
    padding: 50px 80px;
}

.heading {
    text-align: center;
    margin-bottom: 40px;
    color: #111111;
}

.heading h2 {
    font-size: 2rem;
    font-weight: 600;
}

.content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content__img {
    /* padding-top: 1.3rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem; */
    width: 200px;
    height: 200px;
    background-color: #ffffff;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 1rem;
}

.content__img img {
    width: 100%;
    display: block;
    height: auto;
}

.content__header {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

@media screen and (max-width: 650px) {
    .content {
        padding: 0;
    }

    .content__header {
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 500px) {
    .about {
        padding: 60px 30px;
    }
    .content {
        padding: 0;
    }

    .content__header {
        font-size: 1.2rem;
    }
}
